body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-size: 14px;
  padding-top: 20px;
  position: relative;
}

.spacer{
    top:0;
    position: absolute;
    width: 100vw;
    height: 100vh;
    background: rgba(65, 65, 65, 0.22);
    opacity: .5;
    display: none;
    z-index: 3;
}
.scontainer{
    margin:0 5%;
}

.head:nth-child(2){
  font-weight:bold;
  width:85px;
  margin-left:5px;
}
.head:nth-child(3){
  margin-right:5px;
}

div.head{
  display:inline-block;
}

table{
  padding:50px;
  border-collapse: collapse;
}

table.main{
    table-layout:fixed;
}

.main td{
  width:calc(100%/7) !important;
  height:80px;
  padding:0;
  vertical-align:top;
  border-bottom: 1px solid rgba(0, 0, 0, 0.23);
  border-top: 1px solid rgba(0, 0, 0, 0.23);
  outline: none;
}

.main tr td:nth-child(6){
    background-color: rgba(145, 0, 48, 0.1);
}
.main tr td:nth-child(7){
    background-color:rgba(145, 0, 48, 0.1);
}

.main tr td:nth-child(n):hover{
  background-color: #dedede;
  -webkit-transition:0.1s ease-in-out;
  transition:0.1s ease-in-out;
}

.main td:focus-within .numbers p {
  background-color: rgb(201, 201, 201);
  outline: none;
  -webkit-transition:0.3s ease-in-out;
  transition:0.3s ease-in-out;
}

.main td:focus-within .nowDate p {
  background-color: #c70000;
}

.numbers {
  display:block;
  text-align:right;
  padding-right:10px;
  margin:0;
  outline: none;
  font-weight:600;
}

.numbers p{
  width:30px;
  height:30px;
  line-height:30px;
  text-align:center;
  margin: 2px auto;
  margin-right:0;
  border-radius:15px;

}

.nowDate p{
  background-color:red;
  outline: none;
  color:white;
}

.otherMonth{
  color: #b3b3b3;
}

td.holiday{
  background-color:rgba(145, 0, 48, 0.1);
}

p.holiday{
  text-align:left;
  margin:0 2px 0 2px;
  border-radius:5px;
  font-size:12px;
  color:white;
  padding-left:5px;
  background-color: rgba(204, 87, 87, 0.6);
}

button {
    background-color: #dadada;
    border-radius: 24px;
    color: #fff;
    width: 24px;
    padding: 0;
    border: 0;
    font-weight: 200;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    text-decoration: none;
    line-height: 24px;
}
button:hover { background: rgb(53, 167, 110); }

button:active {
  background: rgb(33,147,90);
  box-shadow: 0 3px rgb(33,147,90) inset;
}

button:focus {outline:none !important;}


thead{
  width:200px;
}

.deleteAllEv{
    padding: 10px;
    margin-bottom: 5px;
}
.form{
  display:none;
  position:absolute;
  top:10%;
  left:10%;
  background:white;
  box-shadow:0 0 20px 1px black;
  /*height:40%;*/
  padding:20px;
  z-index:5;
}

.form button{
    margin-bottom:5px;
}



table.dayEvents {
    background:white;
    display:none;
    position:absolute;
    top:5%;
    left:10%;
    width: 85%;
    margin-bottom:50px;
    box-shadow:0 0 20px 1px black;

}

.dayEvents{
    z-index:4;
}

.dayEvents thead tr th:nth-child(2){
    text-align:right;
    position:relative;
}

.dayEvents thead tr th:nth-child(2) div{
    display:inline-block;
}

.dayEvents thead tr th{
    padding:5px;
}


.dayEvents td{
    border-bottom: 1px solid rgba(0, 0, 0, 0.23);
    border-top: 1px solid rgba(0, 0, 0, 0.23);
    outline: none;
    height:30px;
    overflow:hidden;
}


.dayEvents tbody tr td:nth-child(1){
    border:none;
    font-size:10px;
    vertical-align:top;
    text-align:right;
    width:50px;
}

.dayEvents tbody tr td:nth-child(2){
    padding-left:20px;
    height:30px;
}




.dayEvents tbody tr:last-child td{
    height:15px;
    border-bottom: none;

}

.setEvent>div:hover{
    cursor:pointer;
}

.setEvent{
    background-color:indianred;
    color:white;
}

td.setEvent {
    border-left: 3px solid red;
}

.eventDate{
    display:inline-block;
    position:absolute;
    left:40%;
}
.eventDate>div:nth-child(1){
    padding-right:10px;
    font-weight:normal !important;
}

.tasktext{
    width:100%;
}


.submitButton {
    border-radius:10px;
    color:#fff;
    width:80px;
    border: 0;
    font-weight:200;
    text-transform:uppercase;
    cursor:pointer;
    text-decoration:none;
}
.submitButton:hover { background: rgb(53, 167, 110); }

.submitButton:active {
    background: rgb(33,147,90);
    box-shadow: 0 3px rgb(33,147,90) inset;
}

.submitButton:focus {outline:none !important;}

.deleteAllEv{
    width:120px;
    float:right;

}

.additional_events{
    text-align:left;
    margin:0 2px 0 2px;
    border-radius:5px;
    font-size:12px;
    color:white;
    padding-left:5px;
    background-color: rgba(204, 87, 87, 0.6);
    height:20px;
    overflow:hidden;
}


.before{
    background-color: rgba(138, 138, 138, 0.44) !important;
}

.more{
    margin-top:2px;
    background-color: rgba(121, 54, 54, 0.6);
}

.deleteOneEvent{
    width:100px;
    float:right;
    margin-top:3px;
    margin-right:3px;
}

.time_handlers{
    width:150px !important;
    display:inline-block;
    font-weight:bold;
}

.inCellSmall{
    float:left;
    width:calc(100% - 103px);
    overflow:hidden;
    height:30px;
    display:inline-block;
}
.inCellBig{
    width:calc(100% - 103px);
    float:left;
}

@media (min-width:2000px){
    .main td {
        height:150px;
    }

}
